import React from 'react'
// import './hero-section.scss'
import Waypoint from 'react-waypoint'
import scrollToComponent from 'react-scroll-to-component';
import './e-procurement-section.scss'
// import image from './PNG/e-Procurement.png'
import StrategicSourcing from '-!babel!svg-react-loader?name=StrategicSourcing!./ep.svg';
class HeroSection extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            isHeroActive: false
        }
        this._handleWaypointEnter = this._handleWaypointEnter.bind(this);
        this._handleWaypointLeave = this._handleWaypointLeave.bind(this);
    }
    _handleWaypointEnter(){
        console.log("viewing..!");
        scrollToComponent(this.view, { offset: 0, align: 'top', duration: 1000});
        this.setState({
            isHeroActive: true
        })
    }
    _handleWaypointLeave(){
        console.log('viewed..!');
        this.setState({
            isHeroActive: false
        })
    }
    render(){
        return(
        <Waypoint onEnter={this._handleWaypointEnter} onLeave={this._handleWaypointLeave}>
            <div className="section e-procurement-section" ref={(section) => { this.view = section; }}>
                <div className={"container" + (this.state.isHeroActive ? " fade-in" : " fade-out")}>
                    <h1 className="section-title">Procurement can add value to an organisation only if it drives impact on the bottom line.</h1>
                    <StrategicSourcing />
                    <p className="quote">Operating your procurement with our technology can ensure that the savings reach the bottom line. It can support organisational goals far beyond the traditional belief that procurement’s primary role is to meet their needs for goods only.</p>
                </div>
            </div>
        </Waypoint>
        )
    }
}

export default HeroSection;


